html, body {
  background-color: #171717;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.about-container:after {
  content: "";
  pointer-events: none;
  z-index: 1000;
  background: linear-gradient(to top, #171717 0%, #17171700 100%);
  width: 100%;
  height: 10vh;
  position: fixed;
  bottom: 0;
  left: 0;
}

.about-header {
  text-align: left;
}

@media (width <= 768px) {
  .about-header {
    text-align: center;
  }
}

.profile-image-container {
  aspect-ratio: 1024 / 1280;
  width: 50%;
  margin: 0 auto;
  display: block;
  position: relative;
}

.profile-image {
  opacity: 0;
  width: 100%;
  height: auto;
  transition: opacity .4s ease-in-out;
  display: block;
}

.profile-image.loaded {
  opacity: 1;
}

.profile-image-container:before, .profile-image-container:after {
  content: "";
  border: 2px solid #8b0000;
  width: 20px;
  height: 20px;
  position: absolute;
}

.profile-image-container:before {
  border-bottom: none;
  border-right: none;
  top: -6px;
  left: -6px;
}

.profile-image-container:after {
  border-top: none;
  border-left: none;
  bottom: -6px;
  right: -6px;
}

.cv-section h2 {
